:root {
  --main-text-color: #000;
  --edit-text-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  text-align: center;
  text-transform: uppercase;
  justify-content: flex-end;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: var(--main-text-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__list .nav__item:not(:first-child) {
  margin-left: 20px;
}

.is-active, .nav__link:hover {
  color: var(--edit-text-color);
}

.is-active:after {
  content: "";
  background-color: var(--edit-text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

